import { createHomeCard } from "./pageLoader";

function homePage() {
    const content = document.getElementById("content");
    
    //restaurant info div
    const infoContainer = document.createElement("div");
    const restInfoSign = document.createElement("h3");
    const restInfoTxt = document.createElement("p");
    infoContainer.classList.add("card");
    restInfoTxt.textContent = "This pastry shop is the best shop around for all penguins, even you picky King penguins! From IceRolls to FrostedLattes there's something for every type of beak! Come now!";
    restInfoSign.textContent = "- Pingu";
    infoContainer.appendChild(restInfoTxt);
    infoContainer.appendChild(restInfoSign);

    //opening times div
    /*const times = document.createElement("div");
    const timesTitle = document.createElement("h2");
    const timesTxt = document.createElement("p");
    times.classList.add("card");
    timesTitle.classList.add("title");
    timesTxt.classList.add("text");*/
    const {times, timesTitle, timesTxt} = createHomeCard(document);
    timesTxt.setAttribute("id", "open-times");
    timesTitle.textContent = "Hours";
    timesTxt.textContent = "Sunday: 8am - 8pm"
    + "\r\nMonday: 6am - 6pm" + "\r\nTuesday: 6am - 6pm" + "\nWednesday: 6am - 6pm"
    + "\nThursday: 6am - 10pm" + "\nFriday: 6am - 10pm"+ "\nSaturday: 8am - 10pm"
    + "\nSunday: 10am - 4pm";
    timesTxt.innerHTML = timesTxt.innerHTML.replaceAll("\n", "<br>");//safe from XSS injection    
    times.appendChild(timesTitle);
    times.appendChild(timesTxt);

    //location information div
    const locationContainer = document.createElement("div");
    const locationTitle = document.createElement("h2");
    const locationTxt = document.createElement("p");
    locationContainer.classList.add("card");
    locationTitle.classList.add("title");
    locationTxt.classList.add("text");
    locationContainer.appendChild(locationTitle);
    locationContainer.appendChild(locationTxt);


    content.appendChild(infoContainer);
    content.appendChild(times);
}

export {homePage as default};